/* ./src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;
.App {
  text-align: center;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
}
.App select {
  outline: none;
}
ul,
li {
  list-style: none;
}
a {
  font-size: 0.75rem;
  text-decoration: none;
  color: #333;
}
ul {
  display: flex;
  align-items: center;
}
.header__top {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  padding: 0 20px;
}
.header__top ul li {
  padding: 5px;
}
.header__top img {
  width: 10px;
  margin-left: 3px;
}
.header__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px;
}
.header__menu ul li a {
  padding: 10px;
  font-size: 0.8rem;
  font-weight: 600;
}
.logo img {
  width: 140px;
  height: 39px;
}
input {
  font-size: 14px;
  font-weight: 400;
  height: 25px;
  padding: 0 12px;
  background-color: #fff;
  color: #4a4a4a;
  border: 1px solid #e7e7e7;
}
.row {
  padding: 0 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.row li {
  margin: 5px;
  font-weight: 600;
  padding: 3px;
  border-radius: 3px;
}
select {
  border: 1px solid #999;
  border-radius: 3px;
  padding: 6px;
  outline: none;
}
table {
  width: 100%;
  padding: 0 50px;
}
.item-small img {
  width: 18px;
  max-height: 18px;
  overflow: hidden;
  margin-right: 10px;
}
.logo {
  display: flex;
  align-items: center;
}
.item-small {
  display: flex;
  align-items: center;
  padding: 0 10px;
}
.logo h5 {
  font-weight: 400;
}
input {
  border-radius: 3px;
  padding: 16px;
  margin-left: 10px;
  border: 1px solid #999;
  outline: none;
}
a {
  color: #4a4a4a !important;
}
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(137, 167, 98); 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(137, 167, 98); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(137, 167, 98); 
}